import * as React from 'react';
// import { useEffect } from 'react';
import { withStyles } from '@material-ui/core/styles';
import { Grid, FormControlLabel, Typography } from '@material-ui/core/';
import Checkbox, { CheckboxProps } from '@material-ui/core/Checkbox';
// import CloseIcon from '@material-ui/icons/Close';

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faFileUpload, faTimesCircle } from '@fortawesome/free-solid-svg-icons';

// import { TableComponent } from 'Components/UI/TableComponent';
// import { IVerifyMember } from '../IVerifyMember';
import { IApproveMemberModal } from './IApproveMemberModal';

import { SnackbarNotification } from 'Components/UI/SnackbarNotification';

const GreenCheckbox = withStyles({
  root: {
    color: '#272E4C33',
    '&$checked': {
      color: '#1E2071',
    },
  },
  checked: {},
})((props: CheckboxProps) => <Checkbox color="default" {...props} />);

interface ModalContentProps {
  isModalOpen: boolean;
  onApprove: (value: string) => void;
}

export class ModalContent extends React.Component<
  ModalContentProps,
  IApproveMemberModal.IState
> {
  constructor(props: ModalContentProps) {
    super(props);

    const data = {
      birth_certificate: {
        label: 'Birth Certificate',
        checked: false,
        uploaded: false,
        uploaded_file: null,
        uploaded_filename: '',
      },
      baptismal_certificate: {
        label: 'Baptismal Certificate',
        checked: false,
        uploaded: false,
        uploaded_file: null,
        uploaded_filename: '',
      },
      marriage_certificate: {
        label: 'Marriage Certificate',
        checked: false,
        uploaded: false,
        uploaded_file: null,
        uploaded_filename: '',
      },
      solo_parent_id: {
        label: 'Solo Parent ID',
        checked: false,
        uploaded: false,
        uploaded_file: null,
        uploaded_filename: '',
      },
      proof_of_parentage: {
        label: 'Other proof of Parentage',
        checked: false,
        uploaded: false,
        uploaded_file: null,
        uploaded_filename: '',
      },
      certificate_no_marriage: {
        label: 'Certificate of No Marriage',
        checked: false,
        uploaded: false,
        uploaded_file: null,
        uploaded_filename: '',
      },
      annulment_papers: {
        label: 'Annulment Papers',
        checked: false,
        uploaded: false,
        uploaded_file: null,
        uploaded_filename: '',
      },
      passport: {
        label: 'Passport',
        checked: false,
        uploaded: false,
        uploaded_file: null,
        uploaded_filename: '',
      },
      scanned_hmo_card: {
        label: 'Scanned HMO Card',
        checked: false,
        uploaded: false,
        uploaded_file: null,
        uploaded_filename: '',
      },
      death_certificate: {
        label: 'Death Certificate',
        checked: false,
        uploaded: false,
        uploaded_file: null,
        uploaded_filename: '',
      },
      employee_id: {
        label: 'Employee ID',
        checked: false,
        uploaded: false,
        uploaded_file: null,
        uploaded_filename: '',
      },
      proof_overseas_employment: {
        label: 'Proof of Overseas Employment',
        checked: false,
        uploaded: false,
        uploaded_file: null,
        uploaded_filename: '',
      },
      others: {
        label: 'Others',
        checked: false,
        uploaded: false,
        uploaded_file: null,
        uploaded_filename: '',
      },
    };

    this.state = {
      updata: data,
      isSnackbarNotifOpen: false,
      snackbarNotifMsg: 'File successfully uploaded.',
      snackbarNotifVariant: 'success',
    };
  }

  setFieldValue = (fieldName: string, value: any) => {
    this.setState(state => ({
      ...state,
      [fieldName]: value,
    }));

    if (fieldName === 'updata') {
      this.props.onApprove(value);
    }
  };

  setIsSnackbarNotifOpen = (isOpen: boolean) => {
    this.setState({
      isSnackbarNotifOpen: isOpen,
    });
  };

  handleChange = (name: string, checked: boolean) => {
    const temp = this.state.updata;
    temp[name].checked = checked;
    if (!checked) {
      temp[name].uploaded = false;
      temp[name].uploaded_file = null;
      temp[name].uploaded_filename = '';
    }
    this.setFieldValue('updata', temp);
    console.log('name: ', name);
  };

  handleDeleteFile = (name: string) => {
    console.log('event: ', name);
    const temp = this.state.updata;

    temp[name].uploaded = false;
    temp[name].uploaded_file = null;
    temp[name].uploaded_filename = '';
    this.setFieldValue('updata', temp);

    console.log('name: ', name);
  };

  handleUploadFile = (name: string) => {
    console.log('event: ', name);
    const temp = this.state.updata;

    temp[name].uploaded = false;
    this.setFieldValue('updata', temp);
    const inputElement = document.getElementById(name)!;
    if (inputElement) inputElement.click();

    console.log('name: ', name);
  };

  handleUploadFileChange = (event: any) => {
    const name = event.target.id;
    const file = event.target.files[0];
    console.log('handleUploadFileChange event: ', file);
    console.log('handleUploadFileChange name: ', name);
    const temp = this.state.updata;
    temp[name].uploaded = true;
    temp[name].uploaded_filename = file.name;
    temp[name].uploaded_file = file;

    this.setFieldValue('updata', temp);
    this.setIsSnackbarNotifOpen(true);
  };

  public render(): JSX.Element {
    const {
      updata,
      isSnackbarNotifOpen,
      snackbarNotifMsg,
      snackbarNotifVariant,
    } = this.state;

    return (
      <Grid
        container
        direction="column"
        justify="space-evenly"
        alignItems="stretch"
      >
        {Object.keys(updata).map((key, value) => {
          return (
            <Grid container direction="row" justify="space-between" key={key}>
              <FormControlLabel
                control={
                  <GreenCheckbox
                    checked={updata[key].checked}
                    onChange={e => {
                      this.handleChange(key, e.target.checked);
                      console.log('event key: ', key);
                      console.log('event value: ', value);
                    }}
                    value={key}
                  />
                }
                label={updata[key].label}
              />

              {updata[key].uploaded ? (
                <Typography color="textPrimary" style={{ paddingTop: '5px' }}>
                  <span>
                    <span style={{ textDecoration: 'underline' }}>
                      {updata[key].uploaded_filename}
                    </span>
                    <span
                      style={{ paddingLeft: '10px', fontSize: '15px' }}
                      onClick={e => {
                        this.handleDeleteFile(key);
                        console.log(e);
                        console.log('delete: ', key);
                      }}
                    >
                      <FontAwesomeIcon
                        icon={faTimesCircle}
                        style={{ color: '#B5B8C1' }}
                      />
                    </span>
                  </span>
                </Typography>
              ) : updata[key].checked ? (
                <Typography color="textPrimary" style={{ paddingTop: '5px' }}>
                  <span>
                    <span
                      style={{ paddingRight: '10px', fontSize: '15px' }}
                      onClick={e => {
                        this.handleUploadFile(key);
                        console.log(e);
                        console.log('upload: ', key);
                      }}
                    >
                      <FontAwesomeIcon
                        icon={faFileUpload}
                        style={{ color: '#0D5E40' }}
                      />
                    </span>
                    <span
                      style={{ textDecoration: 'underline', color: '#0D5E40' }}
                      onClick={e => {
                        this.handleUploadFile(key);
                        console.log(e);
                        console.log('upload: ', key);
                      }}
                    >
                      Upload File
                    </span>
                    <input
                      id={key}
                      hidden
                      type="file"
                      onChange={e => {
                        this.handleUploadFileChange(e);
                      }}
                    />
                  </span>
                </Typography>
              ) : null}
            </Grid>
          );
        })}
        <SnackbarNotification
          isOpen={isSnackbarNotifOpen}
          message={snackbarNotifMsg}
          variant={snackbarNotifVariant}
          onSnackbarClose={() => {
            this.setIsSnackbarNotifOpen(false);
          }}
        />
      </Grid>
    );
  }
}
