  handleClickCell = (data: any) => {
    if (Object.keys(data).length > 0) {
      this.setLoading(true);
      const { documents_all_data } = this.state;
      for (const cnt in documents_all_data) {
        const temp_data: any = documents_all_data[cnt];
        if (temp_data['_id'] === data['id']) {
          if (data['name'] === 'required') {
            documents_all_data[cnt]['required'] =
              data['value'] === 'true' || data['value'] === true;
          } else if (data['name'] === 'action') {
            if (
              documents_all_data[cnt]['files'] &&
              documents_all_data[cnt]['files'] !== undefined &&
              documents_all_data[cnt]['files'] !== null
            ) {
              documents_all_data[cnt]['files'].push(data['value']);
            } else {
              documents_all_data[cnt]['files'] = [];
              documents_all_data[cnt]['files'].push(data['value']);
            }
          }
          break;
        }
      }
      this.generateDocumentData(documents_all_data, false);
      this.setLoading(false);
    }
  };

  handleSaveDocument = () => {
    this.setState({
      loading: true,
    });
    const payload: any = {
      member_id: this.props.match.params.member_id,
      documents: this.state.documents_all_data,
    };
    API.putMemberDocuments(payload, this.props.match.params.member_id).then(
      response2 => {
        this.setState({
          loading: false,
        });
        if (response2 && response2.error === undefined) {
          this.setState({
            loading: false,
            disableSaveBtn: true,
          });
        } else {
          this.setState({
            loading: false,
            isOpenModal: true,
            modalTitle: 'Error',
            modalMessage: 'Failed to save documents.',
          });
        }
      },
    );
  };

  setFieldValue = (fieldName: string, value: any) => {
    this.setState(state => ({
      ...state,
      [fieldName]: value,
    }));
  };

